import React, { memo } from 'react';
import PropTypes from 'prop-types';
import {
  get as _get, map as _map, size as _size, find as _find, toLower as _toLower, sortBy as _sortBy,
  mapValues as _mapValues
} from 'lodash';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import {
  withStyles, Typography, Paper, Grid,
  FormControl, FormHelperText, TextField, Button
} from '@material-ui/core';
import ErrorIcon from '@material-ui/icons/Error';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import { compose, lifecycle, withState, withHandlers } from 'recompose';
import MomentUtils from '@date-io/moment';
import moment from 'moment';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import TicketsService from '../../services/tickets';
import TicketCategoriesService from '../../services/ticket-categories';
import withRequest from '../../utils/with-request';
import Session from '../../utils/session';
import TicketsEnum from '../../enums/tickets';
import { AGENT_ROLES } from '../../enums/user-roles';
import { QRCODE, PORTAL } from '../../enums/request-sources';

import config from '../../config';
import DescriptionAttachButton from './DescriptionAttachButton';
import styles from './styles';

const { PRIORITIES, PRIORITY_NORMAL } = TicketsEnum;

const emptyTicket = () => {
  const enterpriseUser = Session.getUserData();
  const isAgentUser = AGENT_ROLES.includes(_get(enterpriseUser, 'member.role'));
  const locations = _sortBy(_get(enterpriseUser, 'member.locations', []), ['name']);
  return {
    title: '',
    description: '',
    category_id: '',
    deadline: null,
    priority: isAgentUser ? PRIORITY_NORMAL : null,
    source: PORTAL,
    location_id: _get(locations, '[0].id'),
    attachment_data: null
  };
};

const NewTicket = memo(({
  classes, alert, setAlert, ticket, setTicketState,
  ticketLoading, ticketError, ticketFetch,
  ticketCategoriesLoading, categories,
  isUploading, setIsUploading
}) => {
  const enterpriseUser = Session.getUserData();
  const hasTicketSupport = _get(enterpriseUser, 'member.company.has_ticket_support');
  if (hasTicketSupport) {
    const { showAlert, alertMsg, alertType } = alert;
    const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
    let alertBox = null;
    if (showAlert) {
      alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
    }
    const isAgentUser = AGENT_ROLES.includes(_get(enterpriseUser, 'member.role'));
    const locations = _sortBy(_get(enterpriseUser, 'member.locations', []), ['name']);
    const errors = _mapValues(_get(ticketError, 'errors', {}), (err) => err[0]);
    const isDisabled = ticketCategoriesLoading || ticketLoading;
    const isSubmitDisabled = isDisabled || isUploading;
    return (
      <div className={classes.root}>
        <Header alertBox={alertBox} />
        <section>
          <div className={classes.container}>
            <div className={classes.headerView}>
              <aside>
                <Typography variant="h5">
                  <b>Novo ticket</b>
                </Typography>
              </aside>
            </div>
          </div>
          <br />
          <div className={classes.container}>
            <Paper className={classes.paper}>
              <Typography variant="h6">
                <b>Solicitao</b>
              </Typography>
              <Grid container item xs={12} sm={8} md={6} spacing={2}>
                <Grid item xs={12}>
                  <FormControl fullWidth error={!!errors.title}>
                    <TextField
                      label="Ttulo"
                      error={!!errors.title}
                      className={classes.input}
                      disabled={isDisabled}
                      placeholder="Ex.: Banheiro entupido, mquina de caf com defeito, etc"
                      value={_get(ticket, 'title', '')}
                      onChange={e => setTicketState({ title: e.target.value })}
                      variant="outlined"
                      InputLabelProps={{ shrink: true }}
                    />
                    {errors.title && <FormHelperText>{errors.title}</FormHelperText>}
                  </FormControl>
                  <FormControl fullWidth error={!!errors.comments}>
                    <TextField
                      label="Descrio"
                      multiline
                      rows={4}
                      rowsMax={4}
                      error={!!errors.comments}
                      className={classes.input}
                      disabled={isDisabled}
                      placeholder="Descreva mais detalhes sobre esta ocorrncia..."
                      value={_get(ticket, 'description', '')}
                      onChange={e => setTicketState({ description: e.target.value })}
                      variant="outlined"
                      InputLabelProps={{ shrink: true }}
                    />
                    {errors.comments && <FormHelperText>{errors.comments}</FormHelperText>}
                  </FormControl>
                  {
                    _size(locations) > 1 &&
                      <FormControl fullWidth error={!!errors.location_id}>
                        <TextField
                          select
                          label="Local"
                          error={!!errors.location_id}
                          className={classes.input}
                          disabled={isDisabled}
                          value={_get(ticket, 'location_id')}
                          InputLabelProps={{ shrink: true }}
                          SelectProps={{ native: true }}
                          onChange={e => setTicketState({ location_id: e.target.value })}
                          variant="outlined"
                        >
                          {locations.map(b => <option key={b.id} value={b.id}>{b.name}</option>)}
                        </TextField>
                        {
                          errors.location_id &&
                            <FormHelperText>{errors.location_id}</FormHelperText>
                        }
                      </FormControl>
                  }
                  <FormControl fullWidth error={!!errors.category_id}>
                    <TextField
                      select
                      label="Categoria"
                      error={!!errors.category_id}
                      className={classes.input}
                      disabled={isDisabled}
                      value={_get(ticket, 'category_id')}
                      InputLabelProps={{ shrink: true }}
                      SelectProps={{ native: true }}
                      onChange={e => setTicketState({ category_id: e.target.value })}
                      variant="outlined"
                    >
                      {_map(categories, c => <option key={c.id} value={c.id}>{c.title}</option>)}
                    </TextField>
                    {errors.category_id && <FormHelperText>{errors.category_id}</FormHelperText>}
                  </FormControl>
                </Grid>
              </Grid>
              {
                isAgentUser &&
                  <React.Fragment>
                    <br />
                    <Typography variant="h6">
                      <b>Deadline</b>
                    </Typography>
                    <Grid container item xs={12} sm={8} md={6} spacing={2}>
                      <Grid item xs={12} sm={6}>
                        <FormControl fullWidth>
                          <MuiPickersUtilsProvider
                            libInstance={moment}
                            utils={MomentUtils}
                            locale={config.locale}
                          >
                            <DatePicker
                              autoOk
                              label="Prazo"
                              clearable
                              animateYearScrolling
                              className={classes.input}
                              disabled={isDisabled}
                              invalidDateMessage="Formato invlido"
                              variant="outlined"
                              inputVariant="outlined"
                              InputLabelProps={{ shrink: true }}
                              value={_get(ticket, 'deadline')}
                              onChange={e => setTicketState({ deadline: e })}
                              minDate={new Date()}
                              format="DD/MM/YYYY"
                              cancelLabel="Cancelar"
                              clearLabel="Limpar"
                            />
                          </MuiPickersUtilsProvider>
                        </FormControl>
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <FormControl fullWidth error={!!errors.priority}>
                          <TextField
                            select
                            label="Prioridade"
                            error={!!errors.priority}
                            className={classes.input}
                            disabled={isDisabled}
                            value={_get(ticket, 'priority')}
                            InputLabelProps={{ shrink: true }}
                            SelectProps={{ native: true }}
                            onChange={e => setTicketState({ priority: e.target.value })}
                            variant="outlined"
                          >
                            {PRIORITIES.map(p => (
                              <option value={p.key} key={p.key}>{p.label}</option>
                            ))}
                          </TextField>
                          {errors.priority && <FormHelperText>{errors.priority}</FormHelperText>}
                        </FormControl>
                      </Grid>
                    </Grid>
                  </React.Fragment>
              }
              <br />
              <Typography variant="h6">
                <b>Mais detalhes</b>
              </Typography>
              <br />
              <Grid container item xs={12} sm={8} md={6} spacing={2}>
                <Grid item xs={12}>
                  <div>
                    <DescriptionAttachButton
                      value={_get(ticket, 'attachment_data', null)}
                      onChange={file => {
                        setTicketState({ attachment_data: file });
                        setIsUploading(_get(file, 'progressPercent', 100) < 100);
                      }}
                      disabled={isDisabled}
                    />
                  </div>
                  <div className={classes.actions}>
                    <Button
                      component={Link}
                      to="/tickets"
                      color="default"
                      size="large"
                      disabled={isDisabled}
                      className={classes.cancel}
                    >
                      Voltar
                    </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      size="large"
                      disabled={isSubmitDisabled}
                      onClick={() => ticketFetch(true)}
                    >
                      {isSubmitDisabled ? 'Aguarde...' : 'Enviar'}
                    </Button>
                  </div>
                </Grid>
              </Grid>
            </Paper>
          </div>
        </section>
      </div>
    );
  }
  return (
    <div className={classes.root}>
      <Header />
      <section>
        <div className={classes.container}>
          <Grid container spacing={1}>
            <div className={classes.centerHeaderView}>
              <ErrorIcon className={classes.emptyHeaderIcon} />
              <b className={classes.emptyHeaderTitle}>
                Essa conta no possui o servio de tickets
              </b>
            </div>
          </Grid>
        </div>
      </section>
    </div>
  );
});

NewTicket.propTypes = {
  classes: PropTypes.object,
  categories: PropTypes.array,
  ticket: PropTypes.object,
  ticketError: PropTypes.object,
  ticketFetch: PropTypes.func,
  ticketLoading: PropTypes.bool,
  ticketCategoriesLoading: PropTypes.bool,
  setTicketState: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  isUploading: PropTypes.bool,
  setIsUploading: PropTypes.func
};

const withTicketCategories = withRequest(TicketCategoriesService.getCategories, {
  name: 'ticketCategories',
  options: ({ setTicketState, setCategories, selectedCategory }) => ({
    onRequestFinished: (data) => {
      const categories = _sortBy(_get(data, 'ticket_categories', []), ['title']);
      setCategories(categories);
      const categoryFound = _find(categories, (c) => {
        return _toLower(c.title) === _toLower(selectedCategory);
      });
      setTicketState({ category_id: _get(categoryFound, 'id') || _get(categories, '[0].id') });
    }
  })
});

const withNewTicket = withRequest(TicketsService.postTicket, {
  name: 'ticket',
  init: false,
  options: ({ ticket, setTicketState, setAlert, categories }) => ({
    variables: ticket,
    onRequestFinished: () => {
      setTicketState({ ...emptyTicket(), category_id: _get(categories, '[0].id') });
      setAlert({ alertMsg: 'Ticket registrado com sucesso', alertType: 'success', showAlert: true });
    },
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao registrar esse ticket', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('ticket', 'setTicket', emptyTicket),
  withState('categories', 'setCategories', []),
  withState('isUploading', 'setIsUploading', false),
  withState('selectedCategory', 'setSelectedCategory'),
  withHandlers({
    setTicketState: ({ setTicket, ticket }) => (attrs) => {
      setTicket({ ...ticket, ...attrs });
    }
  }),
  withTicketCategories,
  withNewTicket,
  lifecycle({
    componentDidMount() {
      const { location, setTicketState, setSelectedCategory } = this.props;
      const query = new URLSearchParams(location.search);
      const utmSource = (query.get('utm_source') || '').toUpperCase();
      setSelectedCategory(query.get('category') || query.get('categoria'));
      if (QRCODE === utmSource) {
        setTicketState({ source: QRCODE });
      }
    }
  })
)(NewTicket);
